<template>
  <v-container>
    <div id="app">
      <v-app id="inspire">
        <div>
          <v-sheet tile height="54" color="grey lighten-3" class="d-flex">
            <v-btn icon class="ma-2" @click="$refs.calendar.prev()">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
            <v-select
              v-model="type"
              :items="types"
              dense
              outlined
              hide-details
              class="ma-2"
              label="type"
            ></v-select>
            <v-select
              v-model="mode"
              :items="modes"
              dense
              outlined
              hide-details
              label="event-overlap-mode"
              class="ma-2"
            ></v-select>
            <v-select
              v-model="weekday"
              :items="weekdays"
              dense
              outlined
              hide-details
              label="weekdays"
              class="ma-2"
            ></v-select>
            <v-select
              v-model="facility"
              :items="facilities"
              dense
              outlined
              hide-details
              label="Facility"
              class="ma-2"
              @change="forceUpdate"
            ></v-select>
            <v-spacer></v-spacer>
            <v-btn icon class="ma-2" @click="$refs.calendar.next()">
              <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
          </v-sheet>
          <v-sheet height="600">
            <v-calendar
              ref="calendar"
              v-model="value"
              :weekdays="weekday"
              :type="type"
              :events="events"
              :event-overlap-mode="mode"
              :event-overlap-threshold="30"
              :event-color="getEventColor"
              @change="getEvents"
              @click:event="showEvent"
            ></v-calendar>
            <v-menu
              v-model="selectedOpen"
              :close-on-content-click="false"
              :activator="selectedElement"
              offset-x
            >
              <v-card color="grey lighten-4" min-width="350px" flat>
                <v-toolbar :color="selectedEvent.color" dark>
                  <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-form v-model="valid">
                    <v-text-field v-model="event.firstName" label="First Name"></v-text-field>
                    <v-text-field v-model="event.lastName" label="Last Name"></v-text-field>
                    <v-text-field v-model="event.email" label="Email" :rules="emailRules"></v-text-field>
                    <v-text-field v-model="event.cell" label="Cell Phone" mask="###-###-####"></v-text-field>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-btn text color="secondary" @click="selectedOpen = false">Cancel</v-btn>
                  <v-btn text color="primary" @click="selectedOpen = false">Submit</v-btn>
                </v-card-actions>
              </v-card>
            </v-menu>
          </v-sheet>
        </div>
      </v-app>
    </div>
  </v-container>
</template>

<script>
const data = require("../data/sample.json");

export default {
  name: "HelloWorld",
  data: () => ({
    type: "month",
    types: ["month", "week", "day", "4day"],
    mode: "stack",
    modes: ["stack", "column"],
    weekday: [0, 1, 2, 3, 4, 5, 6],
    weekdays: [
      { text: "Sun - Sat", value: [0, 1, 2, 3, 4, 5, 6] },
      { text: "Mon - Sun", value: [1, 2, 3, 4, 5, 6, 0] },
      { text: "Mon - Fri", value: [1, 2, 3, 4, 5] }
    ],
    value: "",
    events: [],
    colors: ["blue", "orange"],
    facility: "SFH",
    facilities: ["SFH", "SCH"],
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    valid: false,
    event: { firstName: "", lastName: "", email: "", cell: "" },
    emailRules: [
      v => !!v || "E-mail is required",
      v =>
        /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
        "E-mail must be valid"
    ]
  }),
  methods: {
    getEvents({ start, end }) {
      const allDay = false;
      const events = [];

      const min = new Date(`${start.date}T00:00:00`);
      const max = new Date(`${end.date}T23:59:59`);
      const days = (max.getTime() - min.getTime()) / 86400000;

      let currentDateTime = min;

      for (let i = 0; i < days; i++) {
        const currentDate = currentDateTime.toISOString().split("T")[0];

        // loop thru the two shifts
        for (let k = 0; k < data.schedule.length; k++) {
          if (data.schedule[k].facility === this.facility) {
            const first = new Date(
              `${currentDate}T${data.schedule[k].startTime}`
            );
            const second = new Date(
              `${currentDate}T${data.schedule[k].endTime}`
            );

            // check if there's someone who signed up for that date
            const assignedMatch = data.assigned.find(elem => {
              return (
                elem.facility === this.facility &&
                elem.shift === data.schedule[k].shift &&
                elem.date === currentDate
              );
            });
            let matchedName = "";
            if (assignedMatch && assignedMatch.name) {
              matchedName = assignedMatch.name;
            }

            events.push({
              name: matchedName,
              start: this.formatDate(first, !allDay),
              end: this.formatDate(second, !allDay),
              color: this.colors[k]
            });
          }
        }
        currentDateTime.setDate(min.getDate() + 1); // set the next day
      }

      this.events = events;
    },
    getEventColor(event) {
      return event.color;
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    formatDate(a, withTime) {
      return withTime
        ? `${a.getFullYear()}-${a.getMonth() +
            1}-${a.getDate()} ${a.getHours()}:${a.getMinutes()}`
        : `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()}`;
    },
    forceUpdate() {
      // TODO
      console.log("forceUpdate");
      this.$forceUpdate();
      // this.$refs.calendar.change();
      //      this.getEvents();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => (this.selectedOpen = true), 10);
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    }
  }
};
</script>
